
.image img {
  width:70px;
  height:70px;
  }
  
  
  .item {
    width:100%;
    display: flex;
    flex-direction: column;
    gap:10px;
    text-align: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .cart-tab {
    
    max-width: 70%;
    background-color: white;
    inset: 0 0 0 auto;
    display:grid;
    grid-template-rows:  70px 1fr 70px;
    transition: width 2s;
    animation: transition-shrink 1s forwards;
  
  }
  
   
  
  .wrapper {
  
    position: fixed;
    top: 0;
    right: 0;
    width: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    animation: transition 1s forwards;
  
  }
  
  .wrapper-shrink {
  
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    animation: transitionshrink 1s forwards;
  
  }
  @keyframes transition{
    from {width: 0;}
    to {width: 100vw;}
  }
  
  
  
  @keyframes transitionshrink {
    from {width: 100vw;}
    to {width: 0;}
  }
  
  
  
  .cart-tab h1 {
    padding:10px;
    margin: 0;
    font-weight: 300;
    width:100%;
  }
  
  .cart-tab .btn {
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }
  
  .total-price {
    text-align: center;
    align-items: center;
  }
  
  .close-button {
    border: 0;
    background-color: white;
    width:20px;
    padding:10px
  }
  
  
  
  